import { Badge, Button, Card, CardBody, CardTitle, Row, Col , CardText , BreadcrumbItem , Container, Breadcrumb , CardImg} from "reactstrap";
import React, { useState } from "react";
import { useEffect } from "react";
import axios from "axios";
import {
  myaccount,
  myaccountput,
  myaccountput2,
  updatepassword,
  designation,
} from "../../api/user";

const Forms = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirm, setConfirm] = useState("");
  const [profilePic, setProfilePic] = useState("");
  const [id, setId] = useState("");
  const [designation, setDesignation] = useState("");
  const [showPasswordPopup, setShowPasswordPopup] = useState(false);
  const [currentPassword, setCurrentPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [confirmNewPassword, setConfirmNewPassword] = useState("");

  const userid = localStorage.getItem("id");
  const [apiData, setApiData] = useState("");


  useEffect(() => {
    async function callApi() {
      try {
        const apiResult = await myaccount(userid);
        console.log(apiResult);
        console.log(apiResult.fkDesignationId);
        const fkDesignationId = apiResult.fkDesignationId;
        console.log(fkDesignationId);
        setApiData(apiResult.data);
        setName(apiResult.name);
        setEmail(apiResult.email);
        setPassword(apiResult.password);
        setConfirm(apiResult.password);
        setApiData(apiResult);
        setId(apiResult.id);
        setDesignation(apiResult.designationName);
      } catch (error) {
        console.log(error);
      }
    }
    callApi();
  }, []);

  const handleSubmit = (event) => {
    myaccountput2(id, name)
      .then((response) => {
        if (response.status === 200) {
          alert("Your account details have been updated successfully.");
          console.log("User updated successfully:", response.data);
        } else {
          alert("Error updating user.");
          console.error("Error updating user:", response.data);
        }
      })
      .catch((error) => {
        alert(error);
        console.error("Error updating user:", error);
      });
  };

 
  const handlePasswordChangeClick = () => {
    setShowPasswordPopup(true);
  };
  const handlePasswordChangeSubmit = (event) => {
    event.preventDefault();
    if (newPassword === "" || confirmNewPassword === "") {
      alert("Please enter both new password and confirm password.");
      return;
    }
  
    if (newPassword === confirmNewPassword) {
      const userpassword = {
        id: id,
        newPassword: newPassword,
        currentPassword: currentPassword,
      };
      updatepassword(id, newPassword, currentPassword)
        .then((response) => {
        
            alert("Your account details have been updated successfully.");
          console.log("Password updated successfully:", response);
        })
        .catch((error) => {
          alert(error);
          console.error("Error updating user:", error);
        });
    } else {
      alert("Passwords do not match");
    }
    setShowPasswordPopup(false);
  };
  return (
    <div >
      
    <h4 className="mb-3 caliber-font ">My Account Settings</h4>
    
<section >
<Container className="py-5">
<form onSubmit={handleSubmit}>
  <Row>
    <Col lg="4">
      <Card className="mb-4 ">
        <CardBody className="text-center ">
          <CardImg
            src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-chat/ava3.webp"
            alt="avatar"
            className="rounded-circle"
            style={{ width: '150px' }}
            fluid
          />
          <p className="text-muted mb-1 caliber-font">Full Stack Developer</p>
          <p className="text-muted mb-4 caliber-font">Indore , India</p>
          <div className="d-flex justify-content-center mb-2 caliber-font">
            <Button type="submit">Save Changes</Button>
            <Button style={{ marginTop:"9px", fontSize:"13px"}} className="button " onClick={handlePasswordChangeClick}>
              Change Password
            </Button>
          </div>
        </CardBody>
      </Card>
        </Col>
        
        <Col lg="8">
          <Card className="mb-4 caliber-font">
            <CardBody>
              <Row>
                <Col sm="3">
                  <CardText>Full Name</CardText>
                </Col>
                <Col sm="9">
                  <input  value={name}
                    onChange={(event) => setName(event.target.value)} type="text" className="form-control border-0 " placeholder="Enter full name" style={{boxShadow:"none"}} />
                </Col>
              </Row>
              <hr />
              <Row>
                <Col sm="3">
                  <CardText>Email</CardText>
                </Col>
                <Col sm="9">
                  <input  value={email}
                    disabled
                    onChange={(event) => setName(event.target.value)} type="email" className="form-control border-0 " placeholder="Enter email"  style={{boxShadow:"none"}}/>
                </Col>
              </Row>
              <hr />
              <Row>
                <Col sm="3">
                  <CardText>Phone</CardText>
                </Col>
                <Col sm="9">
                  <input type="text" className="form-control border-0 " placeholder="Enter phone number" style={{boxShadow:"none"}} />
                </Col>
              </Row>
              <hr />
              <Row>
                <Col sm="3">
                  <CardText>Mobile</CardText>
                </Col>
                <Col sm="9">
                  <input type="text" className="form-control border-0 " placeholder="Enter mobile number" style={{boxShadow:"none"}} />
                </Col>
              </Row>
              <hr />
            </CardBody>
          </Card>

        </Col>
      </Row>
      </form>
    </Container>
  </section>
    {showPasswordPopup && (
      <div className="password-popup caliber-font">
        <form onSubmit={handlePasswordChangeSubmit}>
          <label for="inp" class="inp">
            <input
              type="password"
              id="inp"
              value={currentPassword}
              onChange={(event) => setCurrentPassword(event.target.value)}
              placeholder="&nbsp;"
            />
            <span class="label">Current Password:</span>
            <span class="focus-bg"></span>
          </label>
          <label for="inp" class="inp">
            <input
              type="password"
              id="inp"
              value={newPassword}
              onChange={(event) => setNewPassword(event.target.value)}
              placeholder="&nbsp;"
            />
            <span class="label">New Password:</span>
            <span class="focus-bg"></span>
          </label>
          <label for="inp" class="inp">
            <input
              type="password"
              id="inp"
              value={confirmNewPassword}
              onChange={(event) => setConfirmNewPassword(event.target.value)}
              placeholder="&nbsp;"
            />
            <span class="label">Confirm New Password:</span>
            <span class="focus-bg"></span>
          </label>
          <button className="popup-button" >
            Change 
          </button>
          <button
            className="popup-button cancel-button"
            
            onClick={() => setShowPasswordPopup(false)}
          >
            Cancel
          </button>
        </form>
      </div>
    )}
  </div>
  );
};

export default Forms;
